<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right" class="el-breadcrumb">
      <el-breadcrumb-item :to="{path:'/main'}">首页</el-breadcrumb-item>
      <el-breadcrumb-item>重要信息管理</el-breadcrumb-item>
      <el-breadcrumb-item>修改信息</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card class="el-card">
      <el-form :model="adminForm" :rules="adminFormRules" ref="adminFormRef" label-width="120px">
        <el-form-item label="账号" prop="adminName">
          <el-input v-model="adminForm.adminName" style="width: 400px" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="adminPassword">
          <el-input v-model="adminForm.adminPassword" type="password" style="width: 400px"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="adminSex">
          <el-input v-model="adminForm.adminSex" style="width: 400px"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" prop="adminPhone">
          <el-input v-model="adminForm.adminPhone" style="width: 400px"></el-input>
        </el-form-item>
        <el-form-item label="邮箱地址" prop="adminEmail">
          <el-input v-model="adminForm.adminEmail" style="width: 400px"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="open">确认修改</el-button>
        </el-form-item>

      </el-form>
    </el-card>

  </div>
</template>

<script>
    export default {
        name: "editMessage",
      data() {
        return {
          adminName: sessionStorage.getItem("adminName"),
          adminForm: {
            id: "",
            adminName: "",
            adminPassword: "",
            adminSex: "",
            adminPhone: "",
            adminEmail: ""
          },
          adminFormRules: {
            adminName: [
              {required: true, message: '不可为空', trigger: 'blur'},
              {min: 1, max: 20, message: '字符长度在2~20个字符之间', trigger: 'blur'}
            ],
            adminPassword: [
              {required: true, message: '不可为空', trigger: 'blur'}
            ],
            adminSex: [
              {required: true, message: '不可为空', trigger: 'blur'}
            ],
            adminPhone: [
              {required: true, message: '不可为空', trigger: 'blur'}
            ],
            adminEmail: [
              {required: true, message: '不可为空', trigger: 'blur'}
            ]
          },
        }
      },
      created() {
        this.queryAdmin()
      },
      methods:{
        queryAdmin() {
          this.$http.get('admin/getAdminByName',
            {params: {adminName:this.adminName}})
            .then(res => {
              this.adminForm = res.data.data;
            });
        },
        editAdmin(){
          this.$refs.adminFormRef.validate(async valid =>{
            if(!valid) return
            const {data:res}=await this.$http.post("admin/editAdmin",this.adminForm)
          })
        },
        open() {
          this.$confirm('此操作将不可逆, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.editAdmin();
            this.queryAdmin();
            this.$message({
              type: 'success',
              message: '修改成功!'
            });
            window.location.reload();
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消修改'
            });
          });
        }
      }
    }
</script>

<style scoped lang="scss">

  .el-breadcrumb {
    margin-bottom: 15px;
    font-size: 17px;
  }
  .el-card {
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
    width: 680px;
  }
</style>



